import React from 'react';
import './Student.css'

const getStudentPoints = (student)=>
{
  let point = student.points;
  if (student.team)
      point += student.team.points;
  return (point);
}

const Student = ({student, index})=>
{
    return (
    <div className="student">
    <div className="student-info">
        <img alt="studentImage" src={`${student.image_url}`}/>
        <div className="info">
            <p className="student-login">{student.name}</p>
            <p className="student-Coalition">{student.coalition.name}</p>
        </div>
    </div>
    <div className="student-ranking">
        <h1>{index + 1}</h1>
        <p className="student-points">{getStudentPoints(student) + student.connections}pts</p>
    </div>
</div>);
}

export default Student;
