import './Coalition.css'
import React from 'react';

//friend requests

const Coalition = ({coal})=>
{
    return (<div className={`coalition`} style={{backgroundColor: coal.color}}>
        <img alt="coalitionImage" className="coalition-image" src={`${coal.image_url}`} />
        <h2>{coal.name.toUpperCase()}</h2>
        <h2>{coal.points}pts</h2>
    </div>);
}

export default Coalition;