import { useEffect, useState} from 'react';
import './App.css';
import Coalition from './Components/Coalition';
import Student from './Components/Student';

const getStudentPoints = (student)=>
{
  let point = student.points +student.connections;
  if (student.team)
      point += student.team.points;
  return (point);
}

function App() {
  const [coalA, setCoalA] = useState(null);
  const [coalB, setCoalB] = useState(null);
  const [coalC, setCoalC] = useState(null);
  const [coalD, setCoalD] = useState(null);
  const [students, setStudents] = useState([]);

  const GetCoalitions = async ()=>
  {
    const coalitions = await (await fetch("http://172.104.226.70:5000/coallition/getCoalitions/")).json();
    setCoalA(coalitions.data[0]);
    setCoalB(coalitions.data[1]);
    setCoalC(coalitions.data[2]);
    setCoalD(coalitions.data[3]);
  }
  
  const GetStudents = async ()=>
  {
    const students = await (await fetch("http://172.104.226.70:5000/students/fetch/all/")).json();
    const data =students.data;
    data.sort((a, b)=> {return getStudentPoints(b) - getStudentPoints(a)})
    setStudents(data);
  }
  useEffect(()=>{
    const interval = setInterval(()=>{
      GetCoalitions();
      GetStudents();
    }, 7000);
    return ()=>{return clearInterval(interval)}
  }, []);
  return ((coalA &&  coalA && coalB && coalC && coalD )
  ? (
    <div className="container">
      <div className="coalitions">
       <Coalition  coal={coalA} />
        <Coalition  coal={coalB} />
        <Coalition  coal={coalC} />
        <Coalition  coal={coalD} />
      </div>
      <div className="students">
      {students.map((student, index) =>{
        return (<Student index={index} student={student} key={student.login} />)
      })
      }
      </div>
    </div>) : <div style={{color:"#fff", textAlign:"center", fontSize:"50px", display:"flex", alignItems:"center", height:"100vh", justifyContent:"center"}}>Waiting....</div>
  );
}

export default App;
